.ellipsis-animation {
    display: inline-block;
    animation: ellipsis 1.5s infinite;
}

@keyframes ellipsis {

    0%,
    20% {
        content: '';
    }

    40% {
        content: '.';
    }

    60% {
        content: '..';
    }

    80%,
    100% {
        content: '...';
    }
}

.transcribing-indicator img {
