I;QACI,qCAAqC;QACrC,uCAAuC;QACvC,sCAAsC;QACtC,gCAAgC;IACpC;;IAEA;QACI,eAAe;QACf,UAAU;IACd;;IAEA;QACI,eAAe;QACf,YAAY;IAChB;AACJ","sourcesContent":["/* Original animations */\r\n.ellipsis-animation {\r\n    display: inline-block;\r\n    animation: ellipsis 1.5s infinite;\r\n}\r\n\r\n@keyframes ellipsis {\r\n\r\n    0%,\r\n    20% {\r\n        content: '';\r\n    }\r\n\r\n    40% {\r\n        content: '.';\r\n    }\r\n\r\n    60% {\r\n        content: '..';\r\n    }\r\n\r\n    80%,\r\n    100% {\r\n        content: '...';\r\n    }\r\n}\r\n\r\n.transcribing-indicator img {\r\n    height: 14px;\r\n}\r\n\r\n/* Enhanced Recording Popup Styles */\r\n.transcription-recording-popup.enhanced {\r\n    position: fixed;\r\n    top: 20px;\r\n    right: 20px;\r\n    width: 320px;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n    color: white;\r\n    border-radius: 16px;\r\n    box-shadow: 0 12px 40px rgba(102, 126, 234, 0.3);\r\n    z-index: 10000;\r\n    font-family: 'Google Sans', 'Roboto', sans-serif;\r\n    animation: slideInFromRight 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);\r\n    overflow: hidden;\r\n}\r\n\r\n@keyframes slideInFromRight {\r\n    from {\r\n        transform: translateX(120%) scale(0.8);\r\n        opacity: 0;\r\n    }\r\n\r\n    to {\r\n        transform: translateX(0) scale(1);\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\n.recording-header {\r\n    padding: 20px 24px 16px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    border-bottom: 1px solid rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.recording-indicator {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n    font-weight: 600;\r\n    font-size: 16px;\r\n}\r\n\r\n.pulse-dot {\r\n    width: 10px;\r\n    height: 10px;\r\n    background: #ff4757;\r\n    border-radius: 50%;\r\n    animation: pulseGlow 1.5s infinite;\r\n    box-shadow: 0 0 10px rgba(255, 71, 87, 0.6);\r\n}\r\n\r\n@keyframes pulseGlow {\r\n\r\n    0%,\r\n    100% {\r\n        opacity: 1;\r\n        transform: scale(1);\r\n        box-shadow: 0 0 10px rgba(255, 71, 87, 0.6);\r\n    }\r\n\r\n    50% {\r\n        opacity: 0.7;\r\n        transform: scale(1.3);\r\n        box-shadow: 0 0 20px rgba(255, 71, 87, 0.8);\r\n    }\r\n}\r\n\r\n.recording-timer {\r\n    font-family: 'Roboto Mono', monospace;\r\n    font-weight: 600;\r\n    font-size: 18px;\r\n    background: rgba(255, 255, 255, 0.15);\r\n    padding: 6px 12px;\r\n    border-radius: 20px;\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n/* Waveform Animation */\r\n.recording-waveform {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 4px;\r\n    padding: 20px;\r\n    height: 60px;\r\n}\r\n\r\n.wave-bar {\r\n    width: 4px;\r\n    background: rgba(255, 255, 255, 0.8);\r\n    border-radius: 2px;\r\n    animation: waveform 1.5s infinite ease-in-out;\r\n}\r\n\r\n@keyframes waveform {\r\n\r\n    0%,\r\n    100% {\r\n        height: 10px;\r\n        opacity: 0.5;\r\n    }\r\n\r\n    50% {\r\n        height: 30px;\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\n.recording-controls {\r\n    padding: 16px 24px;\r\n    display: flex;\r\n    gap: 12px;\r\n    justify-content: center;\r\n}\r\n\r\n.control-btn {\r\n    background: rgba(255, 255, 255, 0.2);\r\n    color: white;\r\n    border: none;\r\n    border-radius: 30px;\r\n    padding: 12px 20px;\r\n    font-size: 14px;\r\n    font-weight: 500;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n    transition: all 0.3s ease;\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n.control-btn:hover {\r\n    background: rgba(255, 255, 255, 0.3);\r\n    transform: translateY(-2px);\r\n}\r\n\r\n.stop-btn {\r\n    background: linear-gradient(45deg, #ff4757, #ff3838);\r\n    flex-grow: 1;\r\n}\r\n\r\n.stop-btn:hover {\r\n    background: linear-gradient(45deg, #ff3838, #ff2818);\r\n    box-shadow: 0 6px 20px rgba(255, 71, 87, 0.4);\r\n}\r\n\r\n.pause-btn {\r\n    width: 48px;\r\n    padding: 12px;\r\n    justify-content: center;\r\n}\r\n\r\n.stop-btn img {\r\n    width: 16px;\r\n    height: 16px;\r\n    filter: brightness(0) invert(1);\r\n}\r\n\r\n.voice-commands,\r\n.recording-tips {\r\n    padding: 8px 24px;\r\n    text-align: center;\r\n}\r\n\r\n.voice-commands {\r\n    background: rgba(255, 255, 255, 0.1);\r\n    border-top: 1px solid rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.voice-commands small,\r\n.recording-tips small {\r\n    color: rgba(255, 255, 255, 0.8);\r\n    font-size: 12px;\r\n}\r\n\r\n/* Enhanced Customization Popup */\r\n.transcription-customization-popup.enhanced {\r\n    position: fixed;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n    width: 500px;\r\n    max-width: 95vw;\r\n    max-height: 90vh;\r\n    background: #fff;\r\n    border-radius: 20px;\r\n    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);\r\n    z-index: 10001;\r\n    font-family: 'Google Sans', 'Roboto', sans-serif;\r\n    animation: popupFadeIn 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);\r\n    overflow: hidden;\r\n}\r\n\r\n.customization-header {\r\n    padding: 24px 28px 20px;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n    color: white;\r\n}\r\n\r\n.customization-header h3 {\r\n    margin: 0 0 12px 0;\r\n    font-size: 20px;\r\n    font-weight: 600;\r\n}\r\n\r\n.processing-status {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n    font-size: 14px;\r\n    background: rgba(255, 255, 255, 0.15);\r\n    padding: 8px 12px;\r\n    border-radius: 20px;\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n.processing-status img {\r\n    width: 16px;\r\n    height: 16px;\r\n}\r\n\r\n.processing-status .success {\r\n    color: #4caf50;\r\n}\r\n\r\n.processing-status .error {\r\n    color: #f44336;\r\n}\r\n\r\n/* Quick Actions */\r\n.quick-actions {\r\n    display: flex;\r\n    gap: 8px;\r\n    margin-bottom: 20px;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n.quick-btn {\r\n    background: #f5f5f5;\r\n    border: 2px solid transparent;\r\n    border-radius: 20px;\r\n    padding: 8px 16px;\r\n    font-size: 13px;\r\n    font-weight: 500;\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n    flex: 1;\r\n    min-width: 100px;\r\n}\r\n\r\n.quick-btn:hover {\r\n    background: #e3f2fd;\r\n    border-color: #1976d2;\r\n}\r\n\r\n.quick-btn.selected {\r\n    background: #1976d2;\r\n    color: white;\r\n    border-color: #1976d2;\r\n}\r\n\r\n/* Form Enhancements */\r\n.customization-form {\r\n    padding: 28px;\r\n    max-height: 500px;\r\n    overflow-y: auto;\r\n}\r\n\r\n.form-row {\r\n    display: flex;\r\n    gap: 16px;\r\n}\r\n\r\n.form-group.half {\r\n    flex: 1;\r\n}\r\n\r\n.form-group {\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.form-group label {\r\n    display: block;\r\n    font-weight: 600;\r\n    color: #333;\r\n    margin-bottom: 8px;\r\n    font-size: 14px;\r\n}\r\n\r\n.form-group input,\r\n.form-group select,\r\n.form-group textarea {\r\n    width: 100%;\r\n    padding: 12px 16px;\r\n    border: 2px solid #e0e0e0;\r\n    border-radius: 12px;\r\n    font-size: 14px;\r\n    font-family: inherit;\r\n    transition: all 0.2s ease;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.form-group input:focus,\r\n.form-group select:focus,\r\n.form-group textarea:focus {\r\n    outline: none;\r\n    border-color: #1976d2;\r\n    box-shadow: 0 0 0 3px rgba(25, 118, 210, 0.1);\r\n}\r\n\r\n.form-group textarea {\r\n    resize: vertical;\r\n    min-height: 60px;\r\n}\r\n\r\n/* Smart Suggestions */\r\n.smart-suggestions {\r\n    margin-top: 4px;\r\n    display: flex;\r\n    gap: 6px;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n.suggestion-chip {\r\n    background: #e3f2fd;\r\n    color: #1976d2;\r\n    padding: 4px 8px;\r\n    border-radius: 12px;\r\n    font-size: 12px;\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.suggestion-chip:hover {\r\n    background: #1976d2;\r\n    color: white;\r\n}\r\n\r\n/* Advanced Opt